            </div>
          </div>
          <div class="textBox">
            <h2>{{ slide.title }}</h2>
            <p class="skills">{{ slide.skills }}</p>
            <p class="introduction" style="white-space: pre-wrap">
              {{ slide.description }}
            </p>
            <a :href="slide.link">프로젝트 링크</a>
            <a :href="slide.git" class="git">git</a>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </section>
</template>
