          link: "",
          // "https://hunyoung529.github.io/react_project/",
          git: "",
          // "https://github.com/hunyoung529/react_project",
          mainImage: require("../assets/maintenance.jpg"),
          smallImages: [
            require("../assets/project2_1.jpg"),
            require("../assets/project2_2.jpg"),
            require("../assets/project2_3.jpg"),
            require("../assets/project2_4.jpg"),
          ],
        },
        {
          id: 3,
